





























































export default {
	data() {
		return {
			isOpen: false,
			isOpenitem: false
		};
	},
	methods: {
		tapMailName() {
			this.$emit('showMail')
		},
		tapOpen() {
			if (this.isOpen) {
				this.isOpenitem = false;
				this.$nextTick(() => {
					this.isOpen = false;
				})
			} else {
				this.isOpen = true;
				this.$nextTick(() => {
					this.isOpenitem = true
				})
			}

		},
		buygoods(){
			this.$emit('buygoods')
		}
	}
}
